.container.love{
      display: flex;
      flex-direction: column;
      align-items: center;
}

.love_content{
      width: 520px;
      height: auto;
      display: flex;
      gap: 2rem;
}

.love_aside{
      width: 160px;
      flex-shrink: 0;
      display: flex;
      justify-content: center;
}

.love_avatar{
      width: 160px;
      height: 160px;
      position:relative;
      display: flex;
      align-items: center;
      justify-content: center;
}

.love_avatar:before,
.love_avatar:after{
      border: solid 2px transparent;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      border-radius: 50%;
}

.love_avatar:before{
      width: 100%;
      height: 100%;
      border-left: solid 2px var(--purple-100);
      animation: rel_1 2s linear infinite;
}

.love_avatar:after{
      width: 154px;
      height: 154px;
      border-right: solid 2px var(--yellow);
      animation: rel_2 2.5s linear infinite;
}

@keyframes rel_1{
      0%{
            transform: translate(-50%, -50%) rotate(0);
      }
      100%{
            transform: translate(-50%, -50%) rotate(360deg);
      }
}

@keyframes rel_2{
      0%{
            transform: translate(-50%, -50%) rotate(0);
      }
      100%{
            transform: translate(-50%, -50%) rotate(-360deg);
      }
}

.love_avatar__img{
      width: 150px;
      height: 150px;
      border-radius: 50%;
      overflow: hidden;

      display: flex;
      align-items: center;
      justify-content: center;
}

.love_data{
      flex-grow: 1;
}

.love_data__infor{
      position: relative;
      padding-left: 3rem;
      padding-top: 6rem;
}

.love_infor__title{
      display: flex;
      height: 30px;
      align-items: center;
      position: relative;
}

.love_infor__title p{
      width: 30px;
      height: 30px;
      display: flex;
      align-items: center;
      justify-content: center;
      position: absolute;
      left: -30px;
}

.love_infor__title p *{
      color: var(--green);
      font-size: 2.5rem;
}

.love_infor__title span{
      color: var(--red-light);
      font-size: 3rem;
}

.love_data__infor strong{
      display: block;
      font-family: var(--ft-1);
      color: var(--purple-200);
      font-size: 3rem;
      font-weight: 300;
}

.love_data__infor a{
      padding: .8rem 2rem;
      font-size: 1.4rem;
      color: var(--light);
      background: var(--purple-300);
      border-radius: 3rem;
      text-transform: uppercase;
}

.love_data__about{
      margin-top: 5rem;
}

.love_data__about p{
      font-size: 1.8rem;
      line-height: 2rem;
}