.load_content{
      position: fixed;
      width: 100%;
      height: 100vh;
      background: var(--purple-400);
      display: flex;
      align-items: center;
      justify-content: center;
      z-index: 1000;
}

.load_content__inner{
      display: flex;
      flex-direction: column;
      align-items: center;
      gap: 2rem;
}

.load_content__inner h1{
      display: flex;
      text-transform: uppercase;
      position: relative;
      align-items: flex-end;
}

.load_content__inner h1 span{
      font-family: var(--ft-1);
      background: var(--grad-title-2);
      font-size: 8rem;
      line-height: 6rem;
      position: relative;
      display: flex;
      animation: top_y 2s ease-in-out infinite;
      color: transparent;
      -webkit-background-clip: text;
      -moz-background-clip: text;
      background-clip: text;
      font-weight: 300;
}

@keyframes top_y{
      0%{
            transform: translateY(0);
      }
      15%{
            transform: translateY(-30px);
      }
      30%{
            transform: translateY(0);
      }
      100%{
            transform: translateY(0);
      }
}

.load_content__inner p{
      font-size: 3rem;
}