@import url('https://fonts.googleapis.com/css2?family=Black+Ops+One&family=Nanum+Gothic+Coding:wght@400;700&family=Rubik+Moonrocks&display=swap');

:root{
  /*  fonts */
  --ft-1: 'Black Ops One', cursive;
  --ft-2: 'Nanum Gothic Coding', monospace;
  --ft-3: 'Rubik Moonrocks', cursive;

  /* color base */
  --purple-500: #141424;
  --purple-400: #000032;
  --purple-300: #342172;
  --purple-200: #586be1;
  --purple-100: #596adc;
  --red-light: #f97b6d;
  --green: #46c167;
  --yellow: #f9dd6d;
  --pink: #ff31d6;
  --light: #fff;

  --grad-banner: linear-gradient(rgba(0,0,0,0), var(--purple-400) 95%);
  --grad-title: linear-gradient(135deg, var(--yellow) 20%, var(--pink), var(--purple-200) 80%);
  --grad-title-2: linear-gradient(var(--yellow) 20%, var(--pink), var(--purple-200) 80%);

  /** social color*/
  --facebook: #3b5995;
  --twitter: #1da1f2;
  --google: #dd4b39;
  --youtube: #c4302b;
  --vimeo: #1ab7ea;
  --dribbble: #ea4c89;
  --github: #333;
  --instagram: #c13584;
  --pinterest: #e60023;
  --flickr: #0063dc;
  --bitbucket: #205081;
  --linkedin: #0e76a8;
  --whatsapp: #12af0a;
  --tumblr: #35465c;
  --tiktok: #222;
  --discord: #5865f2;

  /* global style */
  font-family: var(--ft-2);
  font-size: 62.5%;
}

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:before,
:after{
  content: '';
  position: absolute;
}

body{
  background: var(--purple-400);
  color: var(--light);
  font-size: 16px;
  overflow-x: hidden;
  --p-x: 0;
  --p-y: 0;
}

body *{
  font-size: 1rem;
}

a, span{
  display:  inline-block;
}

a{
  text-decoration: none;
}

ul{
  list-style: none;
}

img{
  width: 100%;
  object-fit: cover;
}

button{
  outline: none;
  border: none;
  cursor: pointer;
}

.container{
  width: 96%;
  max-width: 1024px;
  height: auto;
  margin: 0 auto;
}
