.header_content{
      height: 700px;
      position: relative;
}

.header_banner{
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      overflow: hidden;
}

.header_banner:after{
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      background: var(--grad-banner);
}

.container.header{
      position: relative;
      height: 100%;
      display: flex;
      flex-direction: column;
      align-items: center;
}

.header_title{
      display: inline-block;
      margin-top: 195px;
      position: relative;
      z-index: 2;
}

.header_title h1{
      display: inline-block;
      position: relative;
      background: var(--grad-title);
      font-family: var(--ft-1);
      font-size: 20rem;
      text-transform: uppercase;
      line-height: 16rem;
      color: transparent;
      -webkit-background-clip: text;
      -moz-background-clip: text;
      background-clip: text;
}

.header_title h1[data-title]:before,
.header_title h1[data-title]:after{
      content: attr(data-title);
      top: 0;
      left: 0;
      color: var(--light);
      z-index: -1;
}

.header_title h1[data-title]:before{
      color: var(--purple-400);
      opacity: .5;
      transform: translate(-2rem, -2rem)
}

.header_title h1[data-title]:after{
      opacity: .2;
      transform: translate(2rem, 2rem)
}

.header_description{
      margin-top: 2rem;
}

.header_description p{
      font-size: 3.5rem;
      text-align: center;
}

.header_social{
      margin-top: 2rem;
      display: flex;
      align-items: center;
      justify-content: center;
      height: 40px;
      gap: 1rem;
}

.header_social a{
      width: 40px;
      height: 40px;
      display: flex;
      align-items: center;
      justify-content: center;
      color: var(--light);
      border-radius: 50%;
}

.header_social a *{
      font-size: 1.5rem;
}

.header_social a.facebook{
      background: var(--facebook);
}

.header_social a.twitter{
      background: var(--twitter);
}

.header_social a.instagram{
      background: var(--instagram);
}

.header_social a.youtube{
      background: var(--youtube);
}

.header_social a.tiktok{
      background: var(--tiktok);
}

.header_social a.tumblr{
      background: var(--tumblr);
}

.header_social a.discord{
      background: var(--discord);
}

.header_user{
      width: 100%;
      height: 200px;
      margin-top: 2rem;
      display: flex;
      align-items: center;
      gap: 2rem;
}

.user_box__avatar{
      width: 200px;
      height: 200px;
      position: relative;
      display: flex;
      align-items: center;
      justify-content: center;
}

.user_box__avatar:before,
.user_box__avatar:after{
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      border: solid 2px transparent;
}

.user_box__avatar:before{
      border-left: solid 2px var(--yellow);
      animation: spin1 2s linear infinite;
      border-radius: 50%;
      width: 100%;
      height: 100%;
}

.user_box__avatar:after{
      border-right: solid 2px var(--purple-100);
      animation: spin2 3s linear infinite;
      border-radius: 50%;
      width: 190px;
      height: 190px;
}

@keyframes spin1{
      0%{
            transform: translate(-50%, -50%) rotate(0);
      }
      100%{
            transform: translate(-50%, -50%) rotate(360deg);
      }
}

@keyframes spin2{
      0%{
            transform: translate(-50%, -50%) rotate(0);
      }
      100%{
            transform: translate(-50%, -50%) rotate(-360deg);
      }
}

.user_avatar__img{
      width: 180px;
      height: 180px;
      border-radius: 50%;
      overflow: hidden;
      display: flex;
      align-items: center;
      justify-content: center;
}

.user_infor{
      margin-left: 3rem;
}

.user_infor__title{
      position: relative;
}

.user_infor__title p{
      width: 30px;
      height: 30px;
      position: absolute;
      left: -30px;
      display: flex;
      align-items: center;
      justify-content: center;
}

.user_infor__title p *{
      color: var(--green);
      font-size: 2.5rem;
}

.user_infor__title h3{
      font-size: 3rem;
      color: var(--purple-200);
}

.user_infor__data span{
      font-size: 2.8rem;
      color: var(--red-light);
      font-family: var(--ft-1);
}

.user_infor__data p{
      font-size: 1.8rem;
}

.user_infor__location{
      margin-top: 1rem;
      display: flex;
      align-items: flex-end;
}

.user_infor__location p{
      width: 30px;
      height: 30px;
      position: relative;
      display: flex;
      align-items: center;
      justify-content: center;
      animation: pulse 1s ease-in-out infinite;
}

@keyframes pulse{
      0%{
            transform: translateY(0);
      }
      50%{
            transform: translateY(-1.2rem);
      }
      100%{
            transform: translateY(0);
      }
}

.user_infor__location p *{
      color: var(--red-light);
      font-size: 3rem;
}

.user_infor__location span{
      font-size: 2rem;
}