.page_content{
      position: relative;
      top: 0;
      transform: translateY(100px);
      opacity: 0;
      visibility: hidden;
      margin-top: 5rem;
}

.animated{
      animation: move_y .7s ease-in-out forwards;
}

@keyframes move_y{
      0%{
            transform: translateY(100px);
            opacity: 0;
            visibility: hidden;
      }
      50%{
            transform: translateY(-30px);
            opacity: 1;
            visibility: visible;
      }
      100%{
            transform: translateY(0);
            opacity: 1;
            visibility: visible;
      }
}