.friend_card,
.friend_card a{
      width: 94px;
      height: 94px;
      border-radius: 50%;
      display: flex;
      align-items: center;
      justify-content: center;
      transition: .2s;
}

.friend_card{
      box-shadow: 0 0 0 2px var(--purple-300) inset;
      margin-top: 1rem;
}

.friend_card:hover{
      box-shadow: 0 0 0 3px var(--pink) inset;
}

.friend_card_img{
      width: 80px;
      height: 80px;
      border-radius: 50%;
      display: flex;
      align-items: center;
      justify-content: center;
      overflow: hidden;
}

.friend_card_img img{
      filter: grayscale(1);
      transition: .2s;
}

.friend_card:hover .friend_card_img img{
      filter: grayscale(0);
}


.slick-slide{
      margin: 0 15px;
}

.slick-list{
      height: 120px;
      margin: 0 15px;
}

.slick-slider{
      position: relative;
}

.slick-arrow{
      width: 36px;
      height: 36px;
      position: absolute;
      top: 50%;
      border-radius: 50%;
}

.slick-arrow:before{
      width: 10px;
      height: 10px;
      left: 50%;
      top: 50%;
      color: transparent;
      border-top: solid 2px var(--purple-200);
      border-left: solid 2px var(--purple-200);
}

.slick-arrow.slick-prev:before{
      transform: translate(-50%, -50%) rotate(-45deg);
}

.slick-arrow.slick-next:before{
      transform: translate(-50%, -50%) rotate(135deg);
}


.slick-arrow.slick-prev{
      left: -3rem;
}

.slick-arrow.slick-next{
      right: -2rem;
}