.container.posts{
      display: flex;
      flex-direction: column;
      align-items: center;
}

.post_empty{
      display: flex;
      flex-direction: column;
      align-items: center;
      gap: 2rem;
}

.post_empty img{
      width: 120px;
}

.post_empty h3{
      font-size: 3rem;
      color: var(--red-light);
}

.post_empty p{
      font-size: 2rem;
      text-align:center;
}

.card_post{
      width: 520px;
      height: auto;
      background: var(--purple-100);
      border-radius: 0 0 .6rem .6rem;
      overflow: hidden;
      margin-bottom: 4rem;
}

.card_post__cover{
      width: 100%;
      height: 300px;
      display: flex;
      justify-content: center;
      overflow: hidden;
}

.card_post__cover iframe{
      width: 100%;
      height: 100%;
      border: none;
}

.card_post__content{
      padding: 2rem;
}

.post_content_title h1{
      font-size: 3rem;
      color: var(--yellow);
}

.post_content__description{
      padding: 1.5rem 0;
}

.post_content__description p{
      font-size: 1.6rem;
      line-height: 2rem;
}

.post_content__posted{
      height: 40px;
      display: flex;
      align-items: center;
      justify-content: flex-end;
      position: relative;
}

.post_content__posted:after{
      width: 60%;
      height: 1px;
      left: 0;
      top: 0;
      background: rgba(255,255,255,.2);
}

.post_content__posted span{
      color: var(--purple-400);
      font-size: 1.4rem;
}