.navigator{
      width: calc(100% - 230px);
      margin-left: 230px;
      display: flex;
      gap: 1rem;
      align-items: center;
}

.navigator button{
      padding: 1rem 2rem;
      text-transform: uppercase;
      font-size: 1.2rem;
      border-radius: 2rem;
      background: var(--purple-300);
      color: var(--yellow);
      transition: .2s;
}

.navigator button:hover,
.navigator button.active{
      color: var(--purple-500);
      background: var(--red-light);
}