.container.home{
      display: flex;
      flex-direction: column;
      align-items: center;
}

.friend_content{
      height: 150px;
      width: 500px;
}

.about_content{
      width: 650px;
      display: flex;
      flex-direction: column;
      align-items: center;
}

.about_content_description{
      width: 80%;
}

.about_content_description p{
      text-align: center;
      font-size: 1.9rem;
}

.about_content__menu{
      display: flex;
      align-items: center;
      justify-content: center;
      gap: 2rem;
      margin-top: 3rem;
}

.about_content__menu a{
      width: 158px;
      text-align: center;
      color: var(--yellow);
      background: var(--purple-300);
      padding: 1rem 0;
      font-size: 1.6rem;
      border-radius: 3rem;
}

.video_content{
      width: 620px;
      height: 359px;
      position: relative;
      margin-top: 5rem;
      z-index: 2;
      display: flex;
      align-items: center;
      justify-content: center;
}

.video_content:before,
.video_content:after,
.cornner{
      width: 40%;
      height: 40px;
      background: none;
      position: absolute;
      z-index: -1;
}

.video_content:before,
.video_content:after{
      top:0;
      border-top: solid 1px var(--purple-200);
}

.video_content:before{
      left: 0;
      border-left: solid 1px var(--purple-200);
}

.video_content:after{
      width: 40px;
      height: 40%;
      right: 0;
      border-right: solid 1px var(--purple-200);
}

.cornner:nth-child(1){
      width: 40px;
      height: 40%;
      left: 0;
      bottom: 0;
      border-bottom: solid 1px var(--purple-200);
      border-left: solid 1px var(--purple-200);
}

.cornner:nth-child(2){
      right: 0;
      bottom: 0;
      border-right: solid 1px var(--purple-200);
      border-bottom: solid 1px var(--purple-200);
}

.video_content__box{
      width: 600px;
      height: 339px;
      background: #020202;
      box-shadow: 0 0 0 2px var(--purple-200);
}

.video_content__box iframe{
      width: 100%;
      height: 100%;
      border: none;
}